import React from 'react';
import BreadcrumbCustom from '../BreadcrumbCustom';
import {Card, Col, Row, Icon} from 'antd';
import MyChart from '../common/MyChart';
import LanguageMap from './LanguageMap';
import LanguageGenre from './LanguageGenre';
import LanguagePie from './LanguagePie';
import "../../style/components/dashboard/Dashboard.less";

const trendOpt = {
	tooltip: {
		trigger: 'axis'
	},
	legend: {
		data:['QQ音乐','虾米音乐','网易云音乐','综合数据']
	},
	grid: {
		left: '3%',
		right: '4%',
		bottom: '3%',
		containLabel: true
	},
	toolbox: {
		feature: {
			saveAsImage: {}
		}
	},
	xAxis: {
		type: 'category',
		boundaryGap: false,
		data: ['周五','周六','周日','周一','周二','周三','周四']
	},
	yAxis: {
		type: 'value'
	},
	series: [
		{
			name:'QQ音乐',
			type:'line',
			data:[1379032, 1392384, 1400234, 1410986, 1429876, 1436789, 1449455]
		},
		{
			name:'虾米音乐',
			type:'line',
			data:[450982, 480932, 529873, 555674, 576714, 603751, 669971]
		},
		{
			name:'网易云音乐',
			type:'line',
			data:[40932, 50932, 70921, 82248, 98482, 113728, 138248]
		},
		{
			name:'综合数据',
			type:'line',
			data:[24943, 24943, 24943, 24943, 24943, 24943, 24943]
		},
	]
};

class Dashboard extends React.Component {

	render() {
		return (
			<div className="gutter-example button-demo">
				<BreadcrumbCustom/>
				<Row gutter={16}>
					<Col span={6}>
						<Card title="QQ音乐" bordered={false} className={'board-card'}>
							1449455 条
							<Icon type="coffee" className={'pull-right board-icon qq'}/>
						</Card>
					</Col>
					<Col span={6}>
						<Card title="虾米音乐" bordered={false} className={'board-card'}>
							669971 条
							<Icon type="gift" className={'pull-right board-icon xiami'}/>
						</Card>
					</Col>
					<Col span={6}>
						<Card title="网易云音乐" bordered={false} className={'board-card'}>
							138248 条
							<Icon type="shop" className={'pull-right board-icon wy'}/>
						</Card>
					</Col>
					<Col span={6}>
						<Card title="综合曲库" bordered={false} className={'board-card'}>
							24943 条
							<Icon type="global" className={'pull-right board-icon general'}/>
						</Card>
					</Col>
				</Row>

				<Card title="本周曲库容量走势图" bordered={false} className={"mt"}>
					<MyChart option={trendOpt} height={'400px'}/>
				</Card>

				<Card title="曲库语种分布" bordered={false} className={"mt"}>
					<LanguageMap/>
				</Card>

				<Row gutter={16} className={'mt'}>
					<Col span={14}>
						<Card title="语种与流派统计图" bordered={false} className={'board-card'}>
							<LanguageGenre/>
						</Card>
					</Col>
					<Col span={10}>
						<Card title="各语种占比图" bordered={false} className={'board-card'}>
							<LanguagePie/>
						</Card>
					</Col>
				</Row>

			</div>


		)
	}
}

export default Dashboard;


